<template>
  <div class="hello">
    <h1>{{msg}}</h1>
  </div>
</template>

<script>
export default {
  name: 'hello',
  data () {
    return {
      msg: '待开发'
    }
  }
}
</script>


<style scoped lang="less">
h1, h2 {
  font-weight: normal;
}

</style>



